}
.slide__image img {
  object-fit: cover;
}
.slide__label {
  padding-top: 2rem;
  position: absolute;
  display: inline;
  top: 0;
  right: 0;
  width: auto;
}
.slide__label span {
  border-top-left-radius: 4px;
  border-bottom-left-radius: 4px;
  background: rgb(230, 217, 205);
  font-weight: 500;
  font-size: 1.8rem;
  padding: 0.5rem;
  color: rgb(80, 60, 42);
}
.slide__title {
  user-select: none;
}
.slide__title span {
  display: block;
  color: rgb(80, 60, 42);
}
.slide__content-slide {
  flex: 1 1 auto;
  height: 100%;
}
.slide__item:not(:last-child) {
  margin-bottom: 2rem;
}
.slide__text > span {
  text-transform: uppercase;
}
.slide__text p {
  margin-bottom: 1.6rem;
  text-transform: uppercase;
  line-height: 1.1;
}
.slide__time {
  display: flex;
  gap: 1rem;
  font-weight: 500;
  color: rgb(80, 60, 42);
}
.slide__time span {
  display: flex;
  align-items: center;
}
.slide__data {
  font-size: 1.8rem;
}

.arrow {
  position: relative;
  z-index: 9;
  margin-top: -40px;
  width: auto;
  height: 100px;
  top: 50%;
}
.arrow span {
  display: block;
  width: 15px;
  height: 15px;
  border-bottom: 5px solid var(--font-body-color);
  border-right: 5px solid var(--font-body-color);
  user-select: none;
  pointer-events: none;
  transform: rotate(45deg);
  animation: arrow 2s infinite;
}
.arrow span:nth-child(2) {
  animation-delay: -0.2s;
}
.arrow span:nth-child(3) {
  animation-delay: -0.4s;
}

@keyframes arrow {
  0% {
    opacity: 0;
    transform: translate(0, -20px) rotate(45deg);
  }
  50% {
    opacity: 1;
  }
  100% {
    opacity: 0;
    transform: translate(0, 20px) rotate(45deg);
  }
}