//* ------------------------------- [Variables] --------------------------------
$minWidth: 320px;
$maxWidth: 1920px;
$maxWidthContainer: 1440px;
// $min-height: calc(100% - ($headerHeight + $footerHeight));

//* ---------------------------- [Font Variables] ------------------------------
$font-path: '../fonts';
$font-family-default: 'Sofia Sans Extra Condensed', sans-serif;
$font-family-exclude: 'Ponomar', sans-serif;
$font-weight-default: 400;
$font-size: 1.6rem;
//* ------------------------------ [Font color] --------------------------------
$font-color-default: rgb(9, 9, 27);
$font-color-secondary: rgb(80, 60, 42);
//* --------------------------- [Background Color] -----------------------------
$background: rgb(255, 255, 255);
$color: rgb(230, 217, 205);
$color-secondary: rgb(80, 60, 42);
$border-color: rgba(125, 94, 66, 0.397);
//* ------------------------------- [Shadow] -----------------------------------
$boxShadow: 0px 0px 2px 3px rgba(4, 217, 255, 0.699);
$BlackShadow: rgb(9, 17, 22);
$filter: drop-shadow(1px 0px 3px rgb(32, 25, 71)) invert(5%);
//* ----------------------------------------------------------------------------
$text-selection: rgba(0, 84, 99, 0.247);
$text-color-selection: rgb(155, 153, 153);
//* ----------------------------------------------------------------------------
$dark-Outline: #5c554e;
$darkOutlineThin:
	$dark-Outline 0px 0px 0px,
	$dark-Outline 0.540302px 0.841471px 0px,
	$dark-Outline -0.416147px 0.909297px 0px,
	$dark-Outline -0.989993px 0.14112px 0px,
	$dark-Outline -0.653644px -0.756803px 0px,
	$dark-Outline 0.283662px -0.958924px 0px,
	$dark-Outline 0.96017px -0.279416px 0px;
$darkOutline:
	$dark-Outline 2px 2px 3px,
	$dark-Outline 0.540302px 0.841471px 0px,
	$dark-Outline -0.416147px 0.909297px 0px,
	$dark-Outline -0.989993px 0.14112px 0px,
	$dark-Outline -0.653644px -0.756803px 0px,
	$dark-Outline 0.283662px -0.958924px 0px,
	$dark-Outline 0.96017px -0.279416px 0px;
//* ----------------------------------------------------------------------------
:root {
	--astorRender: url('../img/pictures/i.png');
	--width: calc(0px + (260 - 0) * ((100vw - 320px) / (1024 - 320)));
	--font-color-default: rgb(9, 9, 27);
	--font-color-secondary: rgb(80, 60, 42);
	//* ---------------------------- [Background] --------------------------------
	--background-color: rgb(210, 242, 255);
	--background-second: rgb(2, 10, 16);
	--background-selected: rgba(43, 45, 65, 0.337);
	--background-transparent: rgba(14, 14, 19, 0.855);
	//* ---------------------------- [Scrollbar] ---------------------------------
	--selected-color: rgb(94, 77, 168);
	--themes-color: rgb(167, 167, 167);
	--background-thumb: rgba(86, 56, 20, 0.767);
	--scrollbar-thumb-bgcolor: rgb(231, 231, 231);
	//* -------------------------- [Other colors] --------------------------------
	--border-color: rgba(86, 56, 20, 0.623);
	// ---------------------------------------------------------------------------
	--pos: center center;
	--size: 0;
	--inset-top: 0;
	--inset-right: 100%;
	--inset-bottom: 100%;
	--inset-left: 0;
	// ---------------------------------------------------------------------------
	--stripe-width: 10px;
	--nav-marker-border: rgb(59, 134, 134);
	--sidebar-width: 28.5rem;
	--duration: 0.3s;
	--button-size: 4rem;
	--mob-expanded-width: 320px;
	--mob-collapsed-width: 4rem;
}

$snap: cubic-bezier(1, 0.005, 0.24, 1);
$bounce: cubic-bezier(0.175, 0.885, 0.32, 1.275);
